.card-container {
  border: 1px solid #a0a0a0;
  border-radius: var(--corner-rad);
  margin: var(--card-gap);
  width: var(--max-card-dim);
  height: var(--max-card-dim);
  position: relative;
  -webkit-perspective: 800px;
          perspective: 800px;
  display: inline-block;
}

.card-body {
  border: 2px solid #c0c0c0;
  border-radius: var(--corner-rad);
  width: 100%;
  height: 100%;
  position: absolute;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transform-origin: center center;
      -ms-transform-origin: center center;
          transform-origin: center center;
  -webkit-transition: opacity 1s ease-in, -webkit-transform 0.5s ease-out;
  transition: opacity 1s ease-in, -webkit-transform 0.5s ease-out;
  -o-transition: transform 0.5s ease-out, opacity 1s ease-in;
  transition: transform 0.5s ease-out, opacity 1s ease-in;
  transition: transform 0.5s ease-out, opacity 1s ease-in, -webkit-transform 0.5s ease-out;
}

.card-body figure {
  margin: 0;
  border-radius: var(--corner-rad);
  display: block;
  position: absolute;
  width: var(--max-card-dim);
  height: var(--max-card-dim);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transition: background-color 0.3s ease-out;
  -o-transition: background-color 0.3s ease-out;
  transition: background-color 0.3s ease-out;
}

.card-body .front {
  background-color: #FF652F;
  color: white;
  text-align: center;
  font-weight: bold;
  line-height: var(--max-card-dim);
  font-size: calc(var(--max-card-dim) * 0.6);
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

.card-body .back {
  background-color: #FFE400;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.card-body .back.matched{
  background-color: #14A76C;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.card-body.flipped {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.card-active {
  opacity: 1.0;
}

.card-inactive {
  opacity: 0.0;
}

.card-gif {
  max-height: var(--max-img-dim);  
  max-width: var(--max-img-dim); 
  border: 1px solid #404040;
  width: auto;
  height: auto;
  position: absolute;  
  top: 0;  
  bottom: 0;  
  left: 0;  
  right: 0;  
  margin: auto;
}


